}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .service {
    padding: 40px 0;

    &__list {
      flex-direction: column;
    }

    &__item {
      width: 100%;
      margin-bottom: 40px;

      &:last-child {
        margin-bottom: 0;
      }

    }

    &__item-icon {
      width: 100px;
    }

    &__item-description {
      margin: 0;
      font-size: 32px;
    }

  }

}

