.price {
  background: #f4f4f4;

  &__bg-img {
    position: relative;
    overflow: hidden;

    &:after {
      position: absolute;
      content: "";
      background: url("../images/cube_2.svg") no-repeat;
      top: 10px;
      right: -140px;
      width: 680px;
      height: 610px;
      z-index: 1;
    }

  }

  &__title-h2 {
    margin: 0;
    padding: 90px 0 115px 0;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
    font-size: 44px;
    color: #241f20;
    text-align: center;
  }

  &__title-h3 {
    margin: 0;
    padding: 0 0 60px 0;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
    font-size: 32px;
    color: #241f20;
    text-align: center;
  }

  &__row-description {
    display: flex;
    padding-bottom: 90px;
  }

  &__col-text {
    width: 50%;

    & p {
      font-family: 'Open Sans Condensed', sans-serif;
      font-weight: 700;
      font-size: 28px;
      line-height: normal;
      color: #000;
    }

    & .btn {
      width: 220px;
    }

  }

  &__col-tariff {
    width: 50%;
  }

  &__tariff {
    position: relative;
    width: 250px;
    margin: 0 auto;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    background: #fff;
    z-index: 2;
  }

  &__tariff-title {
    position: relative;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0079e4+0,1852d3+100 */
    background: #0079e4; /* Old browsers */
    background: -moz-linear-gradient(left, #0079e4 0%, #1852d3 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #0079e4 0%,#1852d3 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #0079e4 0%,#1852d3 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0079e4', endColorstr='#1852d3',GradientType=1 ); /* IE6-9 */

    &:after {
      content: "\a0";
      display: block;
      position: relative;
      width: 90%;
      margin-left: auto;
      margin-right: auto;
      bottom: 0;
      padding: 2px 0;
      line-height: 1px;
      border-top: 1px dashed #fff;
    }

    & h3 {
      margin: 0;
      padding: 25px 0;
      font-family: 'Open Sans Condensed', sans-serif;
      font-weight: 700;
      font-size: 34px;
      color: #fff;
      text-align: center;
    }

  }

  &__tariff-description {
    background: #fff;
    text-align: center;

    & ul {
      list-style: none;
      margin: 0;
      padding: 40px;
      font-family: 'Open Sans Condensed', sans-serif;
      font-weight: 700;
      font-size: 28px;
      line-height: normal;
      color: #000;
      text-align: left;

      & span {
        color: #0381ea;
      }

    }

    & .btn {
      display: block;
      margin: 0 50px;
    }

  }

  &__tariff-description-more-link {
    position: relative;
    margin: 15px;
    display: inline-block;
    font-family: 'Open Sans Condensed', sans-serif;
    color: #858585;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

  &__row-tariff {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__table {
    width: 760px;
    border: 2px solid #1852d3;
    border-radius: 15px;
    font-family: 'Open Sans Condensed', sans-serif;
    white-space: nowrap;
  }

  &__table-header {
    padding: 60px 40px 30px 0;
    font-size: 24px;
    font-weight: 700;
    color: #fff;
    border-radius: 12px;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0079e4+0,1852d3+100 */
    background: #0079e4; /* Old browsers */
    background: -moz-linear-gradient(left, #0079e4 0%, #1852d3 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #0079e4 0%,#1852d3 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #0079e4 0%,#1852d3 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0079e4', endColorstr='#1852d3',GradientType=1 ); /* IE6-9 */
  }

  &__table-body {
    padding: 20px 30px 20px 0;
    color: #404040;
    font-size: 28px;
    font-weight: 300;
  }

  &__table-row {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__table-col {
    width: 20%;
    padding: 0 20px;
    text-align: center;
  }

  &__text {
    width: 760px;
    padding: 15px 0;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 300;
    font-size: 18px;
    color: #414042;
    line-height: normal;

    & p {
      padding: 15px 0;
      margin: 0;
    }

    & ul {
      margin: 0;
      padding: 0;
      list-style: none;
    }

    &_btn-box {
      padding: 60px 0;

      & .btn {
        width: 220px;
        margin-right: 15px;
      }

    }

  }

}

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .price {

    &__table {
      width: 700px;
    }

    &__text {
      width: 700px;
    }

  }

}
/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .price {

    &__title-h2 {
      padding: 40px 0;
      font-size: 28px;
    }

    &__title-h3 {
      padding: 0 0 20px 0;
      font-size: 22px;
    }

    &__row-description {
      padding-bottom: 40px;
      flex-direction: column-reverse;
    }

    &__col-text {
      width: 100%;
      text-align: center;

      & p {
        margin: 20px;
        font-size: 22px;
        font-weight: 300;
      }

      & .btn {
        width: 220px;
      }
    }

    &__col-tariff {
      width: 100%;
      padding: 0 40px;
    }

    &__tariff {
      width: 100%;
    }

    &__tariff-title {

      & h3 {
        font-size: 28px;
      }

    }

    &__tariff-description {
      background: #fff;
      text-align: center;

      & ul {
        padding: 20px 40px;
        font-size: 22px;
      }

      & .btn {
        display: block;
        margin: 0 40px;
      }

    }

    &__table {
      width: 100%;
      white-space: normal;
    }

    &__table-header {
      padding: 20px 30px 20px 0;
      font-size: 14px;
    }

    &__table-body {
      padding: 10px 30px 10px 0;
      font-size: 16px;
    }

    &__text {
      width: 100%;
      font-size: 18px;

      &_btn-box {
        padding: 20px 0;

        & .btn {
          white-space: nowrap;
          width: auto;
          margin-bottom: 15px;
        }

      }

    }

  }

}

