.scope {

  &__title {
    margin: 0;
    padding: 90px 0 50px 0;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
    font-size: 44px;
    color: #241f20;
    text-align: center;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 130px;
  }

  &__item {
    width: 33.33%;
    padding: 20px 10px;
    display: flex;
  }

  &__item-icon {
    position: relative;
    top: 8px;
    width: 66px;
    height: 66px;
    display: flex;
    align-items: center;

    & img {
      width: 66px;
      height: 66px;
    }

    &_soon {
      position: relative;

      &:after {
        position: absolute;
        content: "";
        background: url("../images/scope-icon/soon.svg") no-repeat;
        top: -15px;
        left: -10px;
        width: 36px;
        height: 36px;
      }

    }

  }

  &__item-description {
    padding-left: 10px;
    width: 100%;
    display: flex;
    flex-direction: column;
    font-family: 'Open Sans Condensed', sans-serif;

    & h2 {
      margin: 0;
      font-weight: 700;
      font-size: 22px;
      color: #000;
      line-height: normal;
    }

    & p {
      margin: 0;
      font-weight: 300;
      font-size: 18px;
      color: #000;
      line-height: normal;
    }

  }

}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .scope {

    &__item {
      width: 50%;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .scope {

    &__title {
      padding: 40px 0 20px 0;
      font-size: 28px;
    }

    &__list {
      padding-bottom: 40px;
    }

    &__item {
      width: 100%;
    }

    &__item-description {

      & h2 {
        font-size: 20px;
      }

      & p {
        font-size: 16px;
      }

    }

  }

}

