.btn {
  display: inline-block;
  padding: 8px 24px;
  border-radius: 3px;
  font-family: 'Open Sans Condensed', sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: #000;
  text-align: center;
  text-decoration: none;
  border: none;
  cursor: pointer;
}

.btn_blue {
  color: #fff;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0079e4+0,1852d3+100 */
  background: #0079e4; /* Old browsers */
  background: -moz-linear-gradient(left, #0079e4 0%, #1852d3 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #0079e4 0%,#1852d3 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #0079e4 0%,#1852d3 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0079e4', endColorstr='#1852d3',GradientType=1 ); /* IE6-9 */

  &:hover {
    background: #1852d3; /* Old browsers */
    background: -moz-linear-gradient(left, #1852d3 0%, #1852d3 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #1852d3 0%,#1852d3 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #1852d3 0%,#1852d3 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  }

}

.btn_yellow {
  color: #333333;
  background: #ffcb31;
}

.btn_outline {
  color: #414042;
  border: 1px solid #414042 !important;
}
