.navbar {
  padding: 25px;

  &__box {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  &__box {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  &__logo {
    width: 200px;
    height: 40px;
  }

  &__menu-tog {
    display: none;
    margin-left: auto;
    width: 37px;
    height: 26px;
    background: url("../images/menu.svg") no-repeat;
    background-size: 37px auto;
    border: none;
  }

  &__tog {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;

    &_open {
      display: block !important;
    }

  }

  &__menu {
    display: flex;
    flex-direction: row;
    margin: 0 15px;
    padding: 0 0 3px 0;
    list-style: none;

    & li {
      margin: 0 15px;

      & a {
        font-family: 'Myriad Pro', sans-serif;;
        font-size: 18px;
        font-weight: 400;
        color: #585858;
        text-decoration: none;

        &:hover {
          color: #000;
        }

      }

    }

    &_ml {
      margin-left: auto;
    }

    &_mr {
      margin-right: auto;
    }

  }

}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

  .navbar {
    padding: 10px 0;

    &__menu-tog {
      display: inline-block;
    }

    &__tog {
      display: none;
      padding-bottom: 60px;
      position: absolute;
      top: 50px;
      left: 0;
      flex-direction: column;
      background: #fff;
      text-align: center;
      z-index: 999;

      & .btn {
        font-family: 'Myriad Pro', sans-serif;;
        font-size: 24px;
        font-weight: 700;
        color: #0381ea;
        background: #fff;

        &:hover {
          color: #fff;
        }

      }

    }

    &__menu {
      flex-direction: column;
      margin: 0;

      & li {
        margin: 10px 15px;
        text-align: center;

        & a {
          font-size: 24px;

          & span {
            font-weight: 700;
            color: #0381ea;
          }

        }

      }

    }

  }
}

