/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .navbar {
    padding: 10px 0;

    &__logo {
      width: 140px;
      height: 28px;
    }

    &__menu-tog {
      width: 30px;
      height: 19px;
      background-size: 30px 19px;
    }

    &__tog {
      top: 40px;
    }

  }

}

