.header {
  display: flex;
  flex-direction: column;

  &__bg {
    position: relative;
    overflow: hidden;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0079e4+0,1852d3+100 */
    background: #0079e4; /* Old browsers */
    background: -moz-linear-gradient(left, #0079e4 0%, #1852d3 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #0079e4 0%,#1852d3 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #0079e4 0%,#1852d3 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0079e4', endColorstr='#1852d3',GradientType=1 ); /* IE6-9 */
  }

  &__bg-img {
    position: relative;

    &:after {
      position: absolute;
      content: "";
      background: url("../images/cube.svg") no-repeat;
      top: 10px;
      right: 0;
      width: 680px;
      height: 610px;
    }

  }

  &__h1-slogan {
    margin: 0;
    padding: 160px 0 40px 0;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
    font-size: 42px;
    color: #fff;
    text-align: center;

  }

  &__p-slogan {
    margin: 0;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 300;
    font-size: 22px;
    color: #fff;
    text-align: center;
  }

  &__form {
    width: 330px;
    margin: 0 auto;
    padding: 50px 0 70px 0;

    & input {
      padding: 13px 20px;
      margin-bottom: 5px;
      width: 100%;
      border-radius: 3px;
      border: 1px solid rgba(255, 255, 255, 0.7);
      background: rgba(0,0,0,.1);
      font-family: 'Myriad Pro', sans-serif;;
      font-size: 18px;
      font-weight: 400;
      color: #fff;

      &::-webkit-input-placeholder {color:#fff;}
      &::-moz-placeholder          {color:#fff;}/* Firefox 19+ */
      &:-moz-placeholder           {color:#fff;}/* Firefox 18- */
      &:-ms-input-placeholder      {color:#fff;}
    }

    & button {
      width: 100%;
      margin: 20px 0 10px 0;
      font-size: 24px;
    }

    & a {
      font-family: 'Open Sans Condensed', sans-serif;
      font-weight: 300;
      font-size: 18px;
      color: #fff;
    }

  }

}

