/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .header {

    &__h1-slogan {
      padding: 40px 0;
      font-size: 32px;
    }

    &__p-slogan {
      font-size: 18px;
    }

    &__form {
      width: 100%;

      & input {
        padding: 13px 20px;
        font-size: 16px;
      }

      & button {
        margin: 20px 0 10px 0;
        font-size: 22px;
      }

      & a {
        font-family: 'Open Sans Condensed', sans-serif;
        font-weight: 300;
        font-size: 16px;
        color: #fff;
      }

    }

  }

}

